import React, { Component } from "react";
import "./App.css";
import { BrowserRouter as Router, Switch, Route, Redirect, NavLink, useLocation } from "react-router-dom";
import { Contact } from "./Contact";
import { InstrumentListPage } from "./InstrumentListPage";
import { InstrumentSinglePage } from "./InstrumentSinglePage";

export default function App() {
  return (
    <Router>
      <nav className="navbar navbar-expand navbar-dark bg-dark" id="main-nav">
        <div className="navbar-collapse">
          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink
                to={`/react-kurzus/create-react-app?url=/`}
                activeClassName="active"
                exact
                isActive={() => {
                  return window.location.search.split("=")[1] === "/";
                }}
              >
                <span className="nav-link">Hangszerek</span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to={`/react-kurzus/create-react-app?url=/elerhetoseg`}
                activeClassName="active"
                isActive={() => {
                  return window.location.search.split("=")[1] === "/elerhetoseg";
                }}
              >
                <span className="nav-link">Elérhetőség</span>
              </NavLink>
            </li>
          </ul>
        </div>
      </nav>

      <Switch>
        <Route component={WrapperComponent} path="/react-kurzus/create-react-app" />
        <Redirect to={"/react-kurzus/create-react-app?url=/"} />
      </Switch>
    </Router>
  );
}

function WrapperComponent(props) {
  const fakeURL = new URLSearchParams(useLocation().search).get("url");

  if (!fakeURL) {
    return <Redirect to={"/react-kurzus/create-react-app?url=/"} />;
  }

  if (fakeURL === "/elerhetoseg") {
    return <Contact {...props} />;
  }
  if (fakeURL === "/") {
    return <InstrumentListPage {...props} />;
  }
  if (fakeURL.includes("/hangszer/")) {
    return <InstrumentSinglePage userId={fakeURL.split("/")[2]} />;
  }

  return <Redirect to={"/react-kurzus/create-react-app?url=/"} />;
}
