import React, { Fragment, useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

export function InstrumentListPage() {
  const [instruments, setInstruments] = useState([]);
  const [isFetchPending, setFetchPending] = useState(false);

  function fetchInstruments() {
    return fetch(process.env.REACT_APP_USERS_RESOURCE_URL, { credentials: "include" })
      .then((res) => res.json())
      .then(setInstruments)
      .catch(console.log);
  }

  useEffect(() => {
    setFetchPending(true);
    fetchInstruments().finally(() => {
      setFetchPending(false);
    });
  }, []);

  return (
    <div className="p-5  m-auto text-center content bg-ivory">
      {isFetchPending ? (
        <div className="spinner-border"></div>
      ) : (
        <Fragment>
          <h2>Hangszerek:</h2>
          {instruments.map((instrument) => (
            <NavLink
              key={instrument.id}
              to={`/react-kurzus/create-react-app?url=/hangszer/${instrument.id}`}
              activeClassName="active"
            >
              <div className="card col-sm-3 d-inline-block m-1 p-2">
                <h6 className="text-muted">{instrument.brand}</h6>
                <h5 className="text-dark">{instrument.name}</h5>
                <div>{instrument.price} ft -</div>
                <div className="small">Készleten: {instrument.quantity} db</div>
                <div className="card-body">
                  <img className="img-fluid" style={{ maxHeight: 200 }} src={instrument.imageURL ? instrument.imageURL : "https://via.placeholder.com/400x800"} />
                </div>
              </div>
            </NavLink>
          ))}

          <InstrumentCreate fetchInstruments={fetchInstruments}></InstrumentCreate>
        </Fragment>
      )}
    </div>
  );
}

function InstrumentCreate({ fetchInstruments }) {
  return (
    <div className="card p-3 col-sm-12">
      <h2>Új hangszer</h2>
      <form
        onSubmit={(e) => {
          e.persist();
          e.preventDefault();
          fetch(process.env.REACT_APP_USERS_RESOURCE_URL, {
            method: "POST",
            credentials: "include",
            body: JSON.stringify({
              name: e.target.elements.name.value,
              price: e.target.elements.price.value,
              quantity: e.target.elements.quantity.value,
              imageURL: e.target.elements.imageURL.value,
            }),
          })
            .then(() => {
              e.target.reset();
            })
            .then(fetchInstruments);
        }}
      >
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Név:</label>
          <div className="col-sm-9">
            <input type="text" name="name" className="form-control" />
          </div>
        </div>
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Ár:</label>
          <div className="col-sm-9">
            <input type="number" name="price" className="form-control" />
          </div>
        </div>
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Darabszám:</label>
          <div className="col-sm-9">
            <input type="number" name="quantity" className="form-control" />
          </div>
        </div>
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Kép URL:</label>
          <div className="col-sm-9">
            <input type="text" name="imageURL" className="form-control" />
          </div>
        </div>
        <button type="submit" className="btn btn-success">
          Küldés
        </button>
      </form>
    </div>
  );
}
