import React, { useEffect, useState } from "react";

export function InstrumentSinglePage({ userId }) {
  const [instrument, setInstrument] = useState({});
  const [isPending, setPending] = useState(false);

  useEffect(() => {
    setPending(true);

    fetch(`${process.env.REACT_APP_USERS_RESOURCE_URL}/${userId}`, { credentials: "include" })
      .then((res) => res.json())
      .then(setInstrument)
      .catch(console.log)
      .finally(() => {
        setPending(false);
      });
  }, []);

  return (
    <div className="p-5  m-auto text-center content bg-lavender">
      {isPending || !instrument ? (
        <div className="spinner-border"></div>
      ) : (
        <div className="card p-3">
          <div className="card-body">
            <h4>{instrument.brand}</h4>
            <h5 className="card-title">{instrument.name}</h5>
            <div className="lead">
              {instrument.price} ft
            </div>
            <p>Készleten: {instrument.quantity} db</p>
            <img className="img-fluid rounded" style={{maxHeight: "500px"}} 
           src={instrument.imageURL ? instrument.imageURL : "https://via.placeholder.com/400x800"}
             />
          </div>
        </div>
      )}
    </div>
  );
}
